header {
  display: flex;
  justify-content: space-around;
  align-items: center;
  flex-wrap: nowrap;
  background: linear-gradient(90deg, rgba(0,0,0,1) 0%, rgba(9,9,121,1) 35%, rgba(164,22,159,1) 100%);
  color: aliceblue;
  width: 100%;
  height: 10vh;
}

.initial {
  background-color: #222222;
  width: 100%;
  height: fit-content;
  display: flex;
  justify-content:safe;
  align-items: center;
}

.contact {
  color: white;
}

.astronauta {
  width: 50%;
  height: 50%;
}

.acesse {
  text-decoration: none;
  font-size: 2em;
  color: rgb(255, 0, 0);
  display: flex;
  justify-content: center;
}

.acesse2 {
  text-decoration: none;
  font-size: 2em;
  color: rgb(21, 92, 47);
  display: flex;
  justify-content: center;
}

.acesse2:hover {
  background-color: rgb(238, 238, 238);
}

.about-border {
  border: 1px solid black;
  padding: 20px;
}

.inform {
  margin-left: 100px;

}

.button-contact {
  background-color: violet;
  width: 100%;
  height: 25px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.titles {
  color: white;
  justify-content: center;
  display: flex;
  font-size: 3em;
  transition: all .2s ease-in-out;
  animation: 1s ease-out 0s 1 slideInFromLeft;
}

@keyframes slideInFromLeft {
  0% {
    transform: translateX(-100%);
  }
  100% {
    transform: translateX(0);
  }
}

@keyframes fadein {
  from { opacity: 0}
  to   { opacity: 1}
}

.second {
  font-size: 1.2em;
  margin-top: -35px;
  margin-left: 18px;
  color: white;
  animation: 1s ease-out 0s 1 slideInFromRight;
  animation: fadein 2s;
}

.second:hover {
  transform: scale(1.1);
  transition: all .2s ease-in-out;
}

.titles:hover {
  transform: scale(1.1);
  transition: all .2s ease-in-out;
}

.titles:target {
  color:yellow;
}

.links {
  color: white;
  text-decoration: none;
}

.aqui {
  color: rgb(170, 8, 143);
  text-decoration: none;
}

.links:hover {
  color: violet;
}

.projects:hover {
  color: white;
  transform: scale(1.1);
  transition: all .2s ease-in-out;
}

.contact {
  color: white;
  text-decoration: none;
}

.contact:hover {
  color: rgb(255, 255, 255);
  transform: scale(1.1);
  transition: all .2s ease-in-out;
}

.main-text {
  display: flex;
  height: 600px;
  align-items: center;
}

.about-me-project {
  border-image-slice: 1;
  border-width: 5px;
  border-top: 40px solid black;
  border-image-source: linear-gradient(90deg, rgba(0,0,0,1) 0%, rgba(9,9,121,1) 35%, rgba(164,22,159,1) 100%);
  display: flex;
  padding-top: 15px;
  justify-content: center;
  font-size: 30px;
}

.about-me {
  display: flex;
  justify-content: center;
  font-size: 30px;
}

.texto-me {
  font-size: 20px;
}

.texto-me2 {
  font-size: 20px;
  margin-left: 10px;
}

.eu:hover {
  transform: scale(1.10);
  transition: all .2s ease-in-out;
}

.about-text {
  border-image-slice: 1;
  border-width: 5px;
  border-top: 40px solid black;
  border-image-source: linear-gradient(90deg, rgba(0,0,0,1) 0%, rgba(9,9,121,1) 35%, rgba(164,22,159,1) 100%);
  display: flex;
  height: 600px;
  align-items: center;
}

.about-text2 {
  display: flex;
  justify-content:space-around;
  height: 480px;
  align-items:center;
}

.project-text {
  display: flex;
  height: 400px;
  align-items: center;
}

.modo {
  background-color: violet; /* Green */
  color: white;
  border: 2px solid violet;
  font-size: 18px;
  border-radius: 2px;
}

.modo:hover {
  background-color: rgb(248, 177, 248); /* Green */
  color: rgb(255, 255, 255);
  border: 2px solid rgb(248, 177, 248);
  font-size: 18px
}

.beto {
  width: 800px;
  background-color: black;
}

.redes-text {
  border-image-slice: 1;
  border-width: 5px;
  border-top: 40px solid black;
  border-image-source: linear-gradient(90deg, rgba(0,0,0,1) 0%, rgba(9,9,121,1) 35%, rgba(164,22,159,1) 100%);
  display: flex;
  justify-content: center;
  height: 200px;
  align-items: center;
}

.social {
  background-color: white;
  border-radius: 12px;
}

.noturno {
  display: flex;
  justify-content:center;
}

#footer_text {
  display: flex;
  justify-content: center;
  align-items: center;
  color: #ffffff;
  background: linear-gradient(90deg, rgba(0,0,0,1) 0%, rgba(9,9,121,1) 35%, rgba(164,22,159,1) 100%);
  height: 40px;
}

.icons {
  color: black;
}

@media (min-width: 0px) and (max-width: 800px) {

header {
  display: flex;
  justify-content: space-around;
  align-items: center;
  flex-wrap: nowrap;
  background: linear-gradient(90deg, rgba(0,0,0,1) 0%, rgba(9,9,121,1) 35%, rgba(164,22,159,1) 100%);
  color: aliceblue;

}

.appcss {
  display: inline-flex;
  flex-wrap: wrap;
}

#footer_text {
  display: flex;
  justify-content: center;
  align-items: center;
  color: #ffffff;
  width: 100%;
  background: linear-gradient(90deg, rgba(0,0,0,1) 0%, rgba(9,9,121,1) 35%, rgba(164,22,159,1) 100%);
}
}